/* eslint-disable import/prefer-default-export */
import { fromConfiguration } from '../manifest'
import dependencyLoader from '../dependency-loader/dependency-loader'
import cssDependencyLoader from '../dependency-loader/css-dependency-loader'
import { preloadCommons } from '../dependency-loader/preload-dependency'
import { getScriptSrc } from '../../../core/assets'
import requireScript from '../dependency-loader/require-script'

import instrumentation from '../../../common/microfrontend/instrumentation-api'
import { libs } from '../../../core/backbone'

const referenceBag = libs()

export default {
  instrumentation,
  configure: function configure(configuration) {
    this.configuration = {
      ...configuration,
    }

    console.log(configuration)

    referenceBag.timeout = (configuration.assets || {}).timeout || 12000
  },
  async requireMicrofrontend(name, callback, errorCallback) {
    const originalOnError = window.onerror

    window.onerror = (message, source, lineno, colno, error) => {
      errorCallback(error)
      if (originalOnError) originalOnError.apply(this, arguments)
    }

    try {
      const manifestInstance = await fromConfiguration(this.configuration, name)
      const fronthubContext = {
        manifestInstance,
        configuration: this.configuration,
      }
      dependencyLoader(fronthubContext, connect => {
        // this.configuration should be removed only when all microfrontends were migrated.
        const instance = connect.call(
          undefined,
          Object.assign(this.configuration, fronthubContext),
        )
        callback.call(this, instance)
        return instance
      })
      preloadCommons(fronthubContext)
      cssDependencyLoader(fronthubContext)
      await requireScript(
        getScriptSrc(fronthubContext, manifestInstance.key),
        manifestInstance.key,
      )
    } catch (err) {
      errorCallback(err)
    }
  },
}
